body {
    width: 100%;
    height: 100%;
    background-color:rgb(22, 22, 22)
  }
  
  .fade {
    position: relative;
    width: 100%;
    min-height: 60vh;
    top: -25px;
    background-image: linear-gradient(0deg, transparent, black 75%);
    z-index: 1;
  }
  
  .star-wars {
    display: flex;
    justify-content: center;
    position: relative;
    height: 800px;
    color: #feda4a;
    font-family: 'Pathway Gothic One', sans-serif;
    font-size: 500%;
    font-weight: 600;
    letter-spacing: 6px;
    line-height: 150%;
    perspective: 400px;
    text-align: justify;
  }
  
  .crawl {
    position: relative;
    top: 99999px;
    transform-origin: 50% 100%;
    animation: crawl 20s linear;
  }
  
  .crawl > .title {
    font-size: 90%;
    text-align: center;
  }
  
  .crawl > .title h1 {
    margin: 0 0 100px;
    text-transform: uppercase;
  }
  
  @keyframes crawl {
  0% {
      top: -100px;
      transform: rotateX(20deg)  translateZ(0);
    }
  100% { 
      top: -6000px;
      transform: rotateX(25deg) translateZ(-2500px);
      
    }
  }
  .open{
      
      position: absolute;
     
      z-index: 2;
      margin-left: auto;
      margin-right: auto;
      background-color: black;
      border: 4px solid yellow;
      border-radius:10px;
      color:white;
      animation: block 10s linear;
      transition: all 0.3s linear;

  }
  @keyframes block{
    from{
       bottom:50px;
    }
    to{
        bottom:200px; 
    }
  }
  .open:hover{
      background-color:yellow;
      color:black;
  }